/*
 *  Newsletter
 *    Styles for the newsletter section above the footer
 *
 *  Version:
 *    1.0.0 - 2020/11/13
 */
 
@import '../base';

.c-newsletter {
  margin: t-to-rem(41) 0;

  &__main-container{
    overflow: hidden;
    position: relative;
    z-index: 0;

  }

  

  &__image{
    &-container{
      padding-bottom: 121.3%;
      position: relative;
      z-index: -1;
    }
    object-fit: cover;
    @include t-absolute-full;
    height: 100%;
    display: flex;
    align-items: stretch;


    &.is-desktop{
      display: none;

      .has-no-mobile-image &{
        display: block;
      }
    }
    object-fit: cover;
  }


  &__inner{
    &-container{
      @include t-absolute-full;
      display: flex;
      height: 100%;
    }
    
    margin: 8.8% 8%;
    background-color: rgba($s-color--background, .2);
    padding: 15.5% 9.9%;
    backdrop-filter: blur(t-to-rem(32));
    flex: 1;

    &-content {
      text-align: left;
      color: white;
      display: flex;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      max-width: t-to-rem(336);
      margin: 0 auto;
    }
  }

  &__title{    
    margin-bottom: t-to-rem(30);
    color: $s-color--font-6;
  }
  &__text{
    margin: 0 0 t-to-rem(36);
    color: $s-color--font-6;
  }


  &__form{
    border-bottom: 1px solid $s-color--font-4;
    position: relative;
    &::before{
      content: "";
      height: 1px;
      background-color: $s-color--font-1;
      width: 0;
      position: absolute;
      bottom: -1px;
      margin-top: t-to-rem(4);
      left: 0;
      transition: width 1s;
    }

    &:hover,
    &:focus-within {
      &::before {
        width: 100%;
      }
    }
    @include t-anim--transition(border-color, .5s, ease);
    padding-bottom: t-to-rem(16);

    &:hover{
      border-color: $s-color--grey;
    }
    &-form{
      display: flex;
      justify-content: space-between;      
    }
  }

  &__input{
    width: 100%;
    outline: none;
    @include t-font--type-2;
    &::placeholder{
      color: $s-color--white;
    }
    &:focus, &:focus-visible{
      &::placeholder{
        font-style: italic;
      }
    }
  }

  &__submit{
    width: t-to-rem(13);
  }

  &__confirmation{
    text-align: center;
    margin: 1rem auto 3rem;
    color: $s-color--white;
  }

  /** Media Queries ***/
  @include t-media-query($s-small-down) {
    &__title{    
      width: 60%;
    }
    &__form{
      margin-top: auto;
    }
  }

  @include t-media-query($s-small-up) {
    margin: t-to-rem(100) 0;

    &__main-container{
      margin-left: $s-gutter--small;
      margin-right: $s-gutter--small;
    }

    &__image{
      &-container{
        width: 100%;
        padding-bottom: 49.41%;
        width: 100%;
      }
      &.is-mobile{display: none;}
      &.is-desktop{display: block;}
    }

    &__inner{
      text-align: center;
      padding: 5% 0;
      margin: 6%;

      &-content {
        margin: auto;
        text-align: center;
      }
    }
    &__title{
      font-size: t-to-rem(44);
      margin-bottom: t-to-rem(30);
    }
    &__text{
      margin-bottom: t-to-rem(36);
    }
    &__form{
      margin-bottom: t-to-rem(22);
    }

  }
  @include t-media-query($s-medium-up) {
    margin: t-to-rem(120) 0;
    &__main-container{
      margin-left: $s-gutter--medium;
      margin-right: $s-gutter--medium;
    }
    &__inner-content{
      max-width: t-to-rem(473);
    }
    &__title{
      font-size: t-to-rem(54);
      margin-bottom: t-to-rem(62);
    }
    &__text{
      margin-bottom: t-to-rem(72);
    }
    &__form{
      margin-bottom: t-to-rem(44);
    }
  }
  @include t-media-query($s-xlarge-up) {
    margin: t-to-rem(160) 0;
    &__title{
      font-size: t-to-rem(72);
    }
  }

  @include t-media-query($s-xxlarge-up) {
    max-width: t-to-rem(2000);
    margin-left: auto;
    margin-right: auto;
  }

}
